.ToDoListItem{
    display: flex;    
    align-items: center;
}
.ToDoListItem:nth-child(even){
    background-color: aquamarine;
}
.checkbox{
    flex: 1;    
    cursor: pointer;
}
.checkbox.checked{
    color: #eee;    
}
.text.checked{
    color: #eee;
}
.remove{
    flex: 1;
    cursor: pointer;
}
.remove:hover{
    color: red;
}
.text{
    flex: 8;    
}
